        pageSize,
        totalItems,
        total,
        
        // 
        currentView,
        selectedCustomView,
        customViews,
        saveViewDialogVisible,
        viewOptionsDialogVisible,
        renameViewDialogVisible,
        groupField,
        viewForm,
        currentEditingView,
        renameForm,
        groupableFields,
        
        // 
