        updateViewConfig,
        applyViewConfig,
        
        expenseDialogVisible,
        currentTrainData,
        handleExpensePushed,
        handlePushCancelled,
        getTagType,
        handleTagClose,
        memoDialogVisible,
        followUpDialogVisible,
        memoForm,
